@font-face{
    font-family: PopLight;
    src: url(./fonts/Poppins-ExtraLight.ttf);
}

@font-face{
    font-family: PopRegular;
    src: url(./fonts/Poppins-Regular.ttf);
}

body{
    background-image: linear-gradient(to left, #a8edea 0%, #fed6e3 100%);
    font-family: PopLight;
}

.container {
    margin: auto;
    display: flex;
    align-items: center;
    flex-direction: column;
}
  
@media only screen and (max-width: 770px) {
    .container {
        margin: 0 10%;
    }
}

.logo{
    width: 100px;
}

.logo, .menu{
    z-index: 2;
}

nav{
    min-height: 10vh;
    width: 90%;
    margin: auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0px;
}

.page{
    min-height: 20vh;
    display: grid;
    grid-template-columns: 5% 1fr 1fr 1fr 5%;
}

.details{
    grid-column: 2/3;
    align-self: end;
}

.details h1{
    font-size: 44px;
    font-family: PopRegular;
}

.details h2{
    font-size: 32px;
    padding: 20px 0px;
}

.details p{
    font-size: 24px;
    padding: 20px 0px 50px 0px;
}

.infoAPI{
    grid-column: 2/3;
}

.developer p {
    font-size: 18px;
    font-family: PopRegular;
}

.developer{
    grid-column: 2/3;
}