.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 40px;
}

.card{
    margin: 0 10px !important;
}

.infected{
    border-top: 10px solid rgba(255, 255, 0, 0.5);
    border-bottom: 10px solid rgba(255, 255, 0, 0.5);
}

.recovered{
    border-top: 10px solid rgba(105, 255, 0, 0.5);
    border-bottom: 10px solid rgba(105, 255, 0, 0.5);
}

.deaths{
    border-top: 10px solid rgba(255, 8, 0, 0.5);
    border-bottom: 10px solid rgba(255, 8, 0, 0.5);
}